import { css } from '@emotion/react';
import { border, from } from '@guardian/source-foundations';
import { decideContainerOverrides } from './decideContainerOverrides';
export const verticalDivider = (containerPalette) => {
    const containerOverrides = containerPalette && decideContainerOverrides(containerPalette);
    return css `
		${from.tablet} {
			:before {
				content: '';
				display: block;
				position: absolute;
				top: 0;
				bottom: 0;
				left: 0;
				width: 1px;
				height: 100%;
				border-left: 1px solid
					${containerOverrides
        ? containerOverrides.border.container
        : border.secondary};
			}
		}
	`;
};
